const API = {
	get(url, token, data = {}, method = 'GET'){ // get
		return new Promise((resolve,reject) => {
			// 
			url = ``;
			uni.request({
				url,
				method: method,
				data: data,
				header:{ //
					'content-type': 'application/x-www-form-urlencoded',
					'Authorization':  'Bearer '+ token
				},
				success: (res) => {
					if( res.statusCode !== 200 ){
						uni.showToast({
							title:`${res.statusCode}`,
							icon: 'none'
						})
						resolve(res);
						return;
					}
					let data = res.data;
					if( data.code === 200 ){
						resolve(data);
					} else {
						uni.showToast({
							title: '' + data.message,
							icon: 'none'
						});
						resolve(data);
					}	
				},
				fail: (err) => {
					uni.showToast({
						title:'',
						icon: 'none'
					});
					reject(err);
				}
			});
		});
	},
	post(url, token, data = {}){ 	// post
		return new Promise((resolve,reject) => {
			// 
			url = ``;
			uni.request({
				url,
				data,
				method: 'POST',
				header:{ //
					'content-type': 'application/json',
					'Authorization': token
				},
				success: (res) => {
					if( res.statusCode !== 200 ){
						uni.showToast({
							title:`${res.statusCode}`,
							icon: 'none'
						})
						resolve(res);
						return;
					}
					let data = res.data;
					if( data.code === 200 ){
						resolve(data);
					} else{
						uni.showToast({
							title: '' + data.message,
							icon: 'none'
						});
						resolve(data);
					}	
				},
				fail: (err) => {
					uni.showToast({
						title:'',
						icon: 'none'
					});
					reject(err);
				}
			});
		});
	},
	//
	upload(success,fail){
		let url = '';
		// 
		url = ``;
		uni.chooseImage({
			count:1,
			 sourceType: ['album', 'camera'],
			success: (chooseImageRes) => {
				const tempFilePaths = chooseImageRes.tempFilePaths;
				uni.uploadFile({
					url: `${url}/upload/img`, 
					filePath: tempFilePaths[0],
					name: 'file',
					success,
					fail
				});
			}
		});
	},
	// 
	tab(url){
		return new Promise( (resolve,reject) => {
			uni.switchTab({
				url,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			});
		});
	},
	// 
	to(url){
		return new Promise( (resolve,reject) =>{
			uni.navigateTo({
				url,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			})
		});
	},
	//
	bank(delta = 1){
		uni.navigateBack({
			delta
		});
	},
	// 
	setStorage(key,data){
		return new Promise( (resolve,reject) =>{
			uni.setStorage({
				key,
				data,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			})
		})  
	},
	// 
	getStorage(key){
		return new Promise( (resolve,reject) =>{
			uni.getStorage({
				key,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			})
		})
	},
	//
	removeStorage(key){
		return new Promise( (resolve,reject) =>{
			uni.removeStorage({
				key,
				success:(res) =>{
					resolve(res);
				},
				fail:(err) =>{
					reject(err);
				}
			});
		})
	},
	//
	setTitle(title){
		uni.setNavigationBarTitle({
			title,
		});
	},
	disLeftNav(){
		let pages = getCurrentPages();
		let page = pages[pages.length - 1];
		let currentWebview = page.$getAppWebview();
		let titleObj = currentWebview.getStyle().titleNView;
		titleObj.autoBackButton = false;
		currentWebview.setStyle({
			titleNView: titleObj
		});
	}
};
export default API;